import * as ST from './styled'
import { FC, useState } from 'react';
import { Button, Input } from '@src/components';
import { useAuth } from '@providers/AuthProvider';
import { useFormik } from 'formik';

interface LoginForm {
  email: string;
  password: string;
}

const Login: FC = () => {
  const { login } = useAuth();
  const [isLoading, setIsLoading] = useState(false);

  const auth = async (values: LoginForm) => {
    try {
      setIsLoading(true);
      await login(values.email, values.password)
    } catch (e) {
      console.log(e)
    } finally {
      setIsLoading(false);
    }
  }

  const form = useFormik<LoginForm>({
    initialValues: {
      password: '',
      email: '',
    },
    // validationSchema: VALIDATION_SCHEMA,

    onSubmit: async (values, { resetForm }) => {
      auth(values)
    },
  });

  return (
    <ST.Wrapper>
      <ST.Form onSubmit={form.handleSubmit}>
        <ST.Header>
          <ST.Logo />
          <ST.Title>Admin / Авторизация</ST.Title>
        </ST.Header>
        <Input name="email" onChange={form.handleChange} value={form.values.email} caption="Имя пользователя" />
        <Input name="password" onChange={form.handleChange} value={form.values.password} caption="Пароль" type="password" />
        <Button text="Войти" isLoading={isLoading} />
      </ST.Form>
    </ST.Wrapper>
  )
}

export default Login;
